import { Link } from "gatsby"
import React from "react"

import { Container} from "react-bootstrap"

const RoomInfomation02 = () => {
    return (
    <Container fluid>
        <div
          style={{
            margin: `0 0 1.0875rem 0`,
            textAlign: `center`
          }}
        >
          <p style={{ margin: 0, color: `gray`}}> 
            <Link to="/room-information01" className="btn btn-light">카라반</Link>{' '}
            <Link to="/room-information02" className="btn btn-light" style={{color: `deeppink`}}>캠핑방</Link>{' '}
            <Link to="/room-information03" className="btn btn-light">세척실</Link>{' '}
            <Link to="/room-information04" className="btn btn-light">바비큐장/개인수돗가</Link>
          </p>   
        </div>
    </Container>
    );
}

export default RoomInfomation02